@import '../../../common/styles/mixins/mixinCommon';
@import '../../../common/styles/mixins/mixinAdaptive';

.loginContainer {
  @include adaptiveValue('padding', 30, 20, 430, 300, 1);

  display: flex;
  flex-direction: column;
  align-self: center;

  width: 100%;
  max-width: 400px;

  text-align: center;

  background: white;
  border-radius: 20px;
}

.login {
  &__title {
    @include auth_title_style;
  }

  &__form {
    display: flex;
    flex-direction: column;

    margin: 0px 0px 15px 0px;
  }

  &__blockRemember {
    display: flex;
    flex-wrap: wrap;
    row-gap: 10px;
    column-gap: 20px;
    align-items: center;
    justify-content: space-between;

    margin: 0px 0px 15px 0px;

    font-weight: 500;
    @media (max-width: 390px) {
      justify-content: center;
    }
  }

  &__checkBoxBlock {
    margin-right: 0 !important;
    margin-left: -2px !important;
  }

  &__typography {
    @include user_select_none;

    font-weight: 500 !important;
  }

  &__forgotPassword {
    color: blue;
  }

  &__signUpBlock {
    display: flex;
    flex-direction: column;
  }

  &__text {
    margin: 0px 0px 10px 0px;

    font-weight: 500;
    line-height: 18px;
    color: grey;
  }

  &__textEmail {
    margin: 0px 0px 5px 0px;

    font-weight: 500;
    line-height: 18px;
    color: grey;
    text-align: start;
  }

  &__textPassword {
    font-weight: 500;
    line-height: 18px;
    color: grey;
    text-align: start;
  }

  &__buttonsBlock {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__buttonSignIn {
    @include adaptiveValueImportant('padding-left', 30, 20, 430, 300, 1);
    @include adaptiveValueImportant('padding-right', 30, 20, 430, 300, 1);
  }

  &__signUp {
    @include adaptiveValue('padding-left', 30, 20, 430, 300, 1);
    @include adaptiveValue('padding-right', 30, 20, 430, 300, 1);

    display: flex;
    align-items: center;

    height: 38px;
    padding: 5px 30px;

    font-size: 16px;
    font-weight: 500;
    color: black;

    background: white;
    border-radius: 30px;
    box-shadow: 0px 4px 6px rgb(0 0 0 / 20%);

    transition: 0.2s all ease;

    &:hover {
      background: rgb(237 237 237);
    }
  }
}

.checkbox {
  margin: 0 9px 0 0 !important;
  padding: 0 !important;
}

.credentialsColor {
  font-weight: 600;
  color: blue;
}
